<template>
  <div class="page-invite">
    <div class="filter-text">
      <span class="search-icon"><icon name="search"></icon></span>
      <input class="search-input" v-model="searchText" placeholder="请输入患者姓名或关爱码">
    </div>
    <ul class="tab-wrp">
      <li v-for="(tab,index) in tabs" :key="index"
        :class="currentTab===tab.name?'tab-active':''" @click="changeTab(tab.name)"
      > {{tab.text}}</li>
    </ul>
    <component :filterdRecords="sortedRecords" :is="currentTab" keep-alive ></component>
    <div class="no-search" v-show="!sortedRecords.length">
        <img src="../../../assets/HomeView/noResult.png" >
        <p class="no-search-title">{{searchText?'无结果':'暂无发卡记录'}}</p>
    </div>
    <Loading :showLoading="showLoading" />
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import Used from './Used.vue'
import Unuse from './Unuse.vue'
import Loading from '../../Shared/Loading/Loading'
export default {
  name: 'CardRecord',
  components: {
    Used,
    Unuse,
    Loading
  },
  data () {
    return {
      showLoading: true,
      currentTab: 'Used',
      tabs: [{
        name: 'Used',
        text: '已使用'
      },
      {
        name: 'Unuse',
        text: '未使用'
      }],
      filterdRecords: [],
      sortedRecords: [],
      searchText: ''
    }
  },
  watch: {
    cardRecords: function (val, oldVal) {
      this.filterRecords()
      this.showLoading = false
    },
    searchText: function (val, oldVal) {
      this.$store.dispatch('pageClickTracking', {logType: 'input', message: 'Searching: ' + val})
      this.filterRecords()
    }
  },
  computed: {
    ...mapGetters({
      cardRecords: 'cardRecords'
    })
  },
  methods: {
    sortList (a, b) {
      var v1 = a.Sign_Time
      var v2 = b.Sign_Time
      if (v1 == null || v1 === v2) {
        return 1
      } else if (v2 == null) {
        return -1
      } else {
        return (v1 > v2 ? -1 : 1)
      }
    },
    changeTab: function (tab) {
      this.currentTab = tab
      this.$store.dispatch('pageClickTracking', {logType: 'click', message: 'View card record: ' + tab})
    },
    filterRecords: function () {
      this.filterdRecords = []
      for (var i = 0; i < this.cardRecords.length; i++) {
        var temp = this.cardRecords[i]
        if (this.searchText === '' || temp.PatientName.indexOf(this.searchText) !== -1 ||
        temp.Enrollment_Code.indexOf(this.searchText) !== -1) {
          this.filterdRecords.push(temp)
        }
      }
      this.sortedRecords = this.filterdRecords.sort(this.sortList)
    }
  },
  beforeMount () {
    this.$store.dispatch('getCardHistory')
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



