<template>
  <div class="card-history">
    <ul class="card-list">
      <li v-for="(r, index) in filterdRecords" :key="index"
      v-show="r.ShowStatus=='未发放'"
      class="list-row clearfix">
        <div class="card-pic">
          <img :src="avatar" alt="">
        </div>
        <div class="card-info">
          <p>{{r.PatientName}}</p>
          <p>{{r.Enrollment_Code}}</p>
        </div>
        <div class="card-status">
          &nbsp;
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import avatar from '../../../assets/avatar.png'
export default {
  name: 'CardRecord',
  props: ['filterdRecords'],
  components: {
  },
  data () {
    return {
      avatar: avatar
    }
  },
  computed: {
  },
  methods: {
  }
}
</script>
<style lang="scss" scoped>
  @import "./style.scss";

</style>



